<template>
  <div class="swiper-demo">
    <h2></h2>
    
    <!--  -->
    <div class="swiper-button-prev" @click="swiper.slidePrev()"></div>
    <div class="swiper-button-next" @click="swiper.slideNext()"></div>

    <div v-if="banner.length" class="swiper-container">
      <div class="swiper-wrapper">
        <div v-for="item in banner" :key="item.id" class="swiper-slide">
          <img :src="item.imgUrl" class="banner-image">
        </div>
      </div>
      <!--  -->
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
import Swiper from 'swiper';
import 'swiper/css/swiper.min.css'
export default {
  name: 'HelloWorld',
  mounted() {
    this.swiper = new Swiper('.swiper-container', {
      // direction: 'vertical', // 
      loop: true, // 

      // 
      // pagination: {
      //   el: '.swiper-pagination',
      // },
    })
    
    // 2
    setInterval(() => {
      this.swiper.slideNext();
    }, 2000);
  },
  data() {
    return {
      banner: [
        { id: "1", imgUrl: require("@/assets/img/hln3.jpg") },
        { id: "2", imgUrl: require("@/assets/img/crz.jpg") },
        { id: "3", imgUrl: require("@/assets/img/tbw.jpg") },
        { id: "4", imgUrl: require("@/assets/img/wyr.jpg") },
        { id: "5", imgUrl: require("@/assets/img/wzx.jpg") },
        { id: "6", imgUrl: require("@/assets/img/lpy.jpg") },
      ],
      swiper: null,
    }
  },
}
</script>

<style>
.swiper-demo {
  width: 100%;
  margin: 0 auto;
}

.swiper-container {
  width: 20%; /* 20% */
  height: 50%; /* 50% */
  position: relative; /*  */
}

.banner-image {
  width: 100%;
  height: 100%;
}

.swiper-button-prev,
.swiper-button-next {
  position: absolute; /*  */
  top: 50%; /*  */
  transform: translateY(-50%); /*  */
  z-index: 10; /*  */
  cursor: pointer;
}

.swiper-button-prev {
  left: 10px; /* 10px */
}

.swiper-button-next {
  right: 10px; /* 10px */
}
</style>
